/** load **/
.ajax-loading {
  display: none;
}
.ajax-loading .loading {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0 40px;
  height: 80px;
  line-height: 80px;
  background: rgba(0, 0, 0, 0.75);
  border-radius: 6px;
  text-align: center;
  z-index: 9999;
  font-size: 16px;
  color: #fff;
}
.ajax-loading .loading img {
  width: 32px;
  vertical-align: middle;
}
.ajax-loading .loading span {
  margin-left: 12px;
}
.ajax-loading .overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9998;
  background: #ffffff;
  opacity: 0.1;
}
/****/
body {
  margin: 0;
  overflow: hidden;
}
ul,
li {
  list-style: none;
}
input {
  outline: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
[tabindex='-1']:focus {
  outline: none !important;
}
.container {
  flex: 1;
  border-radius: 8px;
  margin: 12px;
  overflow: hidden;
}
.demo-block {
  padding: 12px;
  background: white;
  border: 1px solid #f0f0f0;
}
.demo-title {
  padding-bottom: 12px;
  font-weight: 500;
  font-size: 14px;
}
.description {
  margin-bottom: 20px;
  font-size: 14px;
}
